module minerva.controls.image.measure {
    export interface IInput extends core.measure.IInput {
        source: IImageSource;
        stretch: Stretch;
    }
    export interface IState extends core.measure.IState {
        imageBounds: Rect;
        stretchX: number;
        stretchY: number;
    }

    export class ImageMeasurePipeDef extends core.measure.MeasurePipeDef {
        constructor () {
            super();
            this.addTapinBefore('doOverride', 'calcImageBounds', tapins.calcImageBounds)
                .addTapinBefore('doOverride', 'calcStretch', tapins.calcStretch)
                .replaceTapin('doOverride', tapins.doOverride);
        }

        createState () {
            var state = <IState>super.createState();
            state.imageBounds = new Rect();
            state.stretchX = 0;
            state.stretchY = 0;
            return state;
        }
    }
}
